//base
*,*::before,*::after{
    box-sizing: border-box;
};
img{
    max-width: 100%;
    height: auto;
}
input{
    display: block;
}

//typesetting
.container{
    max-width: $gutters*12 + $space*11;
    margin: 0 auto;
}
.row{
    display: flex;
    margin: 0 auto;
}
.gx-4{
    max-width: $gutters*4 + $space*3;
}
.gx-10{
    max-width: $gutters*10 + $space*9;
}

//font
.font-1-m{
    font-size: $font-size-1;
    font-weight: $font-weight-m;
}
.font-3-b{
    font-size: $font-size-3;
    font-weight: $font-weight-b;
}
//color
.primary{
    color: $primary;
}

//other
.logo{
    a{
        background: url(../images/logo.png);
        height: 235px;
        display: block;
        text-indent: 101%;
        overflow: hidden;
        white-space: nowrap;
        background-size: cover;
        margin-top: 21px;
    }
}
